// chatinput.js

import React, { useState } from "react";
import { FiSend, FiPlus, FiX } from "react-icons/fi"; 
import { api } from "../api/Api";
import "../style/chat.css";

export default function InputChat({ activeConversation, onOpenAddFileModal, onMessageSent, attachedDocumentId, setAttachedDocumentId }) {
    const [message, setMessage] = useState("");
    const [isSending, setIsSending] = useState(false);
    
    // Simplificaﾃｧﾃ｣o: Em um app real, o nome do arquivo viria do state do ChatPage
    const attachedFileName = attachedDocumentId ? `Arquivo ${attachedDocumentId.substring(0, 6)}...` : ''; 

    const handleRemoveAttachment = () => {
        setAttachedDocumentId(null);
    };

    const sendMessage = async () => {
        const userPrompt = message.trim();
        if (!activeConversation || (!userPrompt && !attachedDocumentId) || isSending) return;

        setIsSending(true);
        setMessage("");
        
        // 1. Mensagem do usuﾃ｡rio
        onMessageSent?.({
            role: "user",
            content: userPrompt,
            id: crypto.randomUUID(),
            document_attached_id: attachedDocumentId,
            document_name: attachedDocumentId ? attachedFileName : undefined // Para exibir na bolha do chat
        });

        try {
            const conversationId = activeConversation?._id || activeConversation?.id;

            // 2. Chamada de API de Chat
            const data = await api.sendMessage(userPrompt, conversationId, attachedDocumentId);
            
            handleRemoveAttachment(); // Remove o anexo apﾃｳs o envio (para que o prﾃｳximo nﾃ｣o o use)

            // 3. Resposta do bot
            onMessageSent?.({
                role: "bot",
                content: data?.message_content || data?.content || data?.mensagem || "Resposta da IA nﾃ｣o recebida.",
                id: crypto.randomUUID(),
                generated_document_id: data?.document_id, 
            });

        } catch (err) {
            console.error("Erro ao enviar mensagem:", err);
            
            const errorMessage = "Erro de conexﾃ｣o ou servidor. Tente novamente.";
            onMessageSent?.({ role: "bot", content: errorMessage, id: crypto.randomUUID() });

        } finally {
            setIsSending(false);
        }
    };

    const handleKeyDown = (e) => {
        if (e.key === "Enter" && !e.shiftKey) {
            e.preventDefault();
            sendMessage();
        }
    };

    const placeholderText = isSending ? "Aguardando resposta..." : "Digite sua mensagem...";

    return (
        <div className="input-area-wrapper">
            {/* Barra de visualizaﾃｧﾃ｣o do anexo (como no ChatGPT) */}
            {attachedDocumentId && (
                <div className="attachment-bar">
                    <span className="attachment-name">梼 {attachedFileName}</span>
                    <button className="remove-attachment-btn" onClick={handleRemoveAttachment} title="Remover anexo">
                        <FiX size={16} />
                    </button>
                </div>
            )}
            
            {/* Input Principal */}
            <div className="chat-input">
                <button
                    className="add-file-btn"
                    onClick={onOpenAddFileModal}
                    disabled={isSending}
                >
                    <FiPlus />
                </button>
                <input
                    type="text"
                    placeholder={placeholderText}
                    value={message}
                    onChange={(e) => setMessage(e.target.value)}
                    onKeyDown={handleKeyDown}
                    disabled={isSending}
                />
                <button
                    className="send-btn"
                    onClick={sendMessage}
                    disabled={isSending || (!message.trim() && !attachedDocumentId)}
                >
                    <FiSend />
                </button>
            </div>
        </div>
    );
}