// addfilemodal.js

import React, { useState, useEffect } from "react";
import { MdOutlineClose, MdDriveFolderUpload, MdGeneratingTokens } from "react-icons/md";
import { api } from "../api/Api";
import "../style/add-file.css";

export default function AddFileModal({ templates = [], onClose }) {
    // Nota: Simplificamos o activeConversation, pois o foco ﾃｩ gerar o documento, nﾃ｣o o chat state.
    
    const [selectedFile, setSelectedFile] = useState(null);
    const [selectedTemplateId, setSelectedTemplateId] = useState("");
    const [promptText, setPromptText] = useState("");
    
    // States de fluxo de processamento
    const [isUploading, setIsUploading] = useState(false); 
    const [isGenerating, setIsGenerating] = useState(false); 
    const [error, setError] = useState(null);

    const isProcessing = isUploading || isGenerating;
    // Opcional: Arquivo OU Prompt, mas Template ﾃｩ obrigatﾃｳrio
    const canGenerate = selectedTemplateId && (selectedFile || promptText.trim()) && !isProcessing;

    // Inicializa com o primeiro template se houver
    useEffect(() => {
        if (templates && templates.length > 0 && !selectedTemplateId) {
            setSelectedTemplateId(templates[0].id);
        }
    }, [templates]);

    // Lida com a seleﾃｧﾃ｣o de arquivo do <input type="file">
    const handleFileSelect = (e) => {
        const file = e.target.files[0];
        if (file) {
            setSelectedFile(file);
            setError(null);
        }
    };

    // Funﾃｧﾃ｣o central para Orquestrar: Upload -> Geraﾃｧﾃ｣o -> Download
    const handleGenerateDocument = async () => {
        if (!canGenerate) {
            setError("Selecione um template e forneﾃｧa um arquivo OU uma instruﾃｧﾃ｣o.");
            return;
        }

        setError(null);
        let inputDocumentId = null;

        try {
            // 1. UPLOAD do Arquivo (Se o usuﾃ｡rio selecionou um)
            if (selectedFile) {
                setIsUploading(true);
                const uploadResponse = await api.uploadDocument(selectedFile);
                inputDocumentId = uploadResponse.file_id || uploadResponse?.document?._id; 
                
                if (!inputDocumentId) {
                    throw new Error("Falha ao obter ID do documento apﾃｳs upload.");
                }
                setIsUploading(false);
            }

            // 2. GERAﾃﾃグ do Documento pela IA
            setIsGenerating(true);
            
            const generateData = await api.generateDocument(
                selectedTemplateId,
                inputDocumentId,
                promptText.trim()
            );

            const generatedFileId = generateData.file_id || generateData.document_id; 

            if (!generatedFileId) {
                throw new Error("ID do documento gerado nﾃ｣o recebido da API.");
            }
            setIsGenerating(false);

            // 3. DOWNLOAD do Arquivo Gerado (Reutiliza a lﾃｳgica do DocumentLink)
            const blob = await api.downloadFile(generatedFileId);
            
            const url = window.URL.createObjectURL(blob);
            const a = document.createElement('a');
            a.href = url;
            const templateName = templates.find(t => t.id === selectedTemplateId)?.nome || selectedTemplateId;
            a.download = `Documento_Gerado_${templateName.replace(/\s/g, '_')}.docx`; 
            document.body.appendChild(a);
            a.click();
            document.body.removeChild(a); 
            window.URL.revokeObjectURL(url); 

            alert("Documento gerado e download iniciado com sucesso!");
            onClose(); 

        } catch (err) {
            console.error("Erro no processo de geraﾃｧﾃ｣o:", err);
            setError(err.message || "Erro desconhecido ao gerar o documento."); 
            setIsUploading(false); 
            setIsGenerating(false);
        }
    };

    return (
        <div className="modal-overlay">
            <div className="modal add-file-modal modal-animate">
                <div className="modal-header">
                    <h3>Gerar Documento com Template</h3>
                    <button className="close-modal-btn" onClick={onClose} disabled={isProcessing}><MdOutlineClose /></button>
                </div>
                <div className="modal-content">
                    {error && <p className="error-message error-box">{error}</p>}

                    {/* 1. SELEﾃﾃグ DO TEMPLATE */}
                    <div className="template-select-group">
                        <label htmlFor="template-select">Escolha o **Template Base**:</label>
                        <select
                            id="template-select"
                            value={selectedTemplateId}
                            onChange={(e) => setSelectedTemplateId(e.target.value)}
                            disabled={isProcessing || !templates.length}
                        >
                            {templates.length > 0 ? (
                                templates.map((t) => (
                                    <option key={t.id} value={t.id}>{t.nome}</option>
                                ))
                            ) : (
                                <option value="" disabled>Carregando Templates...</option>
                            )}
                        </select>
                    </div>
                    
                    {/* 2. UPLOAD DO ARQUIVO DE ENTRADA (Opcional) */}
                    <label className={`file-upload-label ${isProcessing ? "disabled" : ""}`} htmlFor="file-upload">
                        <MdDriveFolderUpload className="upload-icon" />
                        <span>
                            {isProcessing ? "Aguarde..." : (selectedFile ? selectedFile.name : "Clique para selecionar o **Arquivo de Dados** (Opcional)")}
                        </span>
                        <input 
                            id="file-upload"
                            type="file" 
                            onChange={handleFileSelect} 
                            disabled={isProcessing} 
                            style={{ display: "none" }} 
                        />
                        {selectedFile && (
                            <button className="remove-file-btn" onClick={(e) => {e.stopPropagation(); setSelectedFile(null);}}>X</button>
                        )}
                    </label>

                    {/* 3. PROMPT DE INSTRUﾃﾃグ (Opcional) */}
                    <div className="prompt-input-group">
                        <label htmlFor="prompt-text">**Instruﾃｧﾃ｣o** para a IA (Opcional):</label>
                        <textarea
                            id="prompt-text"
                            placeholder="Ex: 'Extraia o nome do contrato e a data de validade.' ou 'Escreva um resumo de 3 parﾃ｡grafos e preencha o campo RESUMO.' (Obrigatﾃｳrio se nﾃ｣o houver arquivo)."
                            value={promptText}
                            onChange={(e) => setPromptText(e.target.value)}
                            rows="3"
                            disabled={isProcessing}
                        />
                    </div>
                </div>

                {/* BOTﾃグ DE Aﾃﾃグ */}
                <div className="modal-footer">
                    <button 
                        className="generate-btn" 
                        onClick={handleGenerateDocument} 
                        disabled={!canGenerate}
                    >
                        {isUploading ? "Enviando Arquivo..." : 
                         isGenerating ? (<span><MdGeneratingTokens className="spinner-icon" /> Gerando Documento...</span>) : 
                         "Gerar Documento"}
                    </button>
                </div>
            </div>
        </div>
    );
}