import React from "react";
import { api } from "../api/Api";

export default function DocumentLink({ documentId, onRemove }) {
  if (!documentId) return null;

  const handleDownload = async () => {
    try {
      const blob = await api.downloadFile(documentId);
      const url = window.URL.createObjectURL(blob);

      const a = document.createElement("a");
      a.href = url;
      a.download = `documento_${documentId}.pdf`; 
      document.body.appendChild(a);
      a.click();

      window.URL.revokeObjectURL(url);
      a.remove();
    } catch (err) {
      alert("Erro ao baixar documento: " + err.message);
    }
  };

  return (
    <div
      style={{
        display: "flex",
        alignItems: "center",
        backgroundColor: "#f1f5f9",
        padding: "6px 12px",
        borderRadius: "8px",
        marginTop: "8px",
      }}
    >
      <span style={{ flex: 1, fontSize: "0.9rem" }}>梼 Documento anexado</span>

      <button
        onClick={handleDownload}
        style={{
          marginRight: "8px",
          backgroundColor: "#2563eb",
          color: "white",
          border: "none",
          padding: "4px 8px",
          borderRadius: "6px",
          cursor: "pointer",
          fontSize: "0.8rem",
        }}
      >
        Baixar
      </button>

      {onRemove && (
        <button
          onClick={onRemove}
          style={{
            backgroundColor: "#ef4444",
            color: "white",
            border: "none",
            padding: "4px 8px",
            borderRadius: "6px",
            cursor: "pointer",
            fontSize: "0.8rem",
          }}
        >
          Remover
        </button>
      )}
    </div>
  );
}
